body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@0,100..700;1,100..700&display=swap');

.g-root_theme_light {
    
    --g-font-family-sans: 'Inter', 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;
    --g-font-family-monospace: 'Roboto Mono', 'Menlo', 'Monaco', 'Consolas', 'Ubuntu Mono', 'Liberation Mono', 'DejaVu Sans Mono', 'Courier New', 'Courier', monospace;
    
    --g-color-private-brand-50: rgba(127,86,217,0.1);
        --g-color-private-brand-100: rgba(127,86,217,0.15);
        --g-color-private-brand-150: rgba(127,86,217,0.2);
        --g-color-private-brand-200: rgba(127,86,217,0.3);
        --g-color-private-brand-250: rgba(127,86,217,0.4);
        --g-color-private-brand-300: rgba(127,86,217,0.5);
        --g-color-private-brand-350: rgba(127,86,217,0.6);
        --g-color-private-brand-400: rgba(127,86,217,0.7);
        --g-color-private-brand-450: rgba(127,86,217,0.8);
        --g-color-private-brand-500: rgba(127,86,217,0.9);
        --g-color-private-brand-550-solid: rgb(127,86,217);
        --g-color-private-brand-1000-solid: rgb(48,38,61);
        --g-color-private-brand-950-solid: rgb(53,40,71);
        --g-color-private-brand-900-solid: rgb(62,46,89);
        --g-color-private-brand-850-solid: rgb(71,52,107);
        --g-color-private-brand-800-solid: rgb(81,58,126);
        --g-color-private-brand-750-solid: rgb(90,63,144);
        --g-color-private-brand-700-solid: rgb(99,69,162);
        --g-color-private-brand-650-solid: rgb(108,75,180);
        --g-color-private-brand-600-solid: rgb(118,80,199);
        --g-color-private-brand-500-solid: rgb(140,103,221);
        --g-color-private-brand-450-solid: rgb(153,120,225);
        --g-color-private-brand-400-solid: rgb(165,137,228);
        --g-color-private-brand-350-solid: rgb(178,154,232);
        --g-color-private-brand-300-solid: rgb(191,171,236);
        --g-color-private-brand-250-solid: rgb(204,187,240);
        --g-color-private-brand-200-solid: rgb(217,204,244);
        --g-color-private-brand-150-solid: rgb(229,221,247);
        --g-color-private-brand-100-solid: rgb(236,230,249);
        --g-color-private-brand-50-solid: rgb(242,238,251);
        
        
        --g-color-base-brand: #7F56D9;
        --g-color-base-background: rgb(255,255,255);
        --g-color-base-brand-hover: var(--g-color-private-brand-600-solid);
        --g-color-base-selection: var(--g-color-private-brand-200);
        --g-color-base-selection-hover: var(--g-color-private-brand-300);
        --g-color-line-brand: var(--g-color-private-brand-600-solid);
        --g-color-text-brand: var(--g-color-private-brand-700-solid);
        --g-color-text-brand-heavy: var(--g-color-private-brand-700-solid);
        --g-color-text-brand-contrast: rgb(255, 255, 255);
        --g-color-text-link: var(--g-color-private-brand-600-solid);
        --g-color-text-link-hover: var(--g-color-private-brand-800-solid);
        --g-color-text-link-visited: var(--g-color-private-purple-550-solid);
        --g-color-text-link-visited-hover: var(--g-color-private-purple-800-solid);
}

